:root {
  /* Global properties for .app-ui container */
  --app-ui-width: 88vw;
  --app-ui-height: 83vh;
  --app-ui-margin: 5%;
  --app-ui-bg-color: #fff;
  --app-ui-border-radius: 30px;
  --app-ui-box-shadow: 2px 14px 40px -20px rgba(0, 0, 0, 0.2);

  /* Properties for .right-ui component */
  /* --right-ui-width: 50vw;
  --right-ui-height: 60vh;
  --right-ui-left: 435px;
  --right-ui-top: 60px;
  --right-ui-bg-color: #bbe6f3;
  --right-ui-border-radius: 20px;
  --right-ui-box-shadow: -6px 14px 40px -20px rgba(0, 0, 0, 0.2); */

  /* Properties for .album-img component */
  --album-img-width: 200px;
  --album-img-height: 200px;
  --album-img-scale-hover: 1.02;
  --album-img-transition: transform 0.3s ease;


}


/* Define variables for the player card */
:root {
  --player-card-bg-color: #fff;
  --player-card-padding: 20px;
  --player-card-border-radius: 15px;
  --player-card-box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  --player-card-margin-bottom: 10px;
}

/* Style for player-card */
.player-card {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  background-color: var(--player-card-bg-color);
  padding: var(--player-card-padding);
  border-radius: var(--player-card-border-radius);
  box-shadow: var(--player-card-box-shadow);
  margin-bottom: var(--player-card-margin-bottom);
  top: 58vh;
}



.player-info {
  display: flex;
  align-items: center;
}



.song-detail {
  display: flex;
  flex-direction: column;
}




.blue-heart {
  margin-left: auto; /* Aligns the heart icon to the far right */
}


.playlists {
  display: flex;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch; /* Smooth scrolling on iOS */
  scrollbar-width: none; /* Hide scrollbar for Firefox */
}

.playlists::-webkit-scrollbar {
  display: none; /* Hide scrollbar for Chrome, Safari, and Edge */
}

.album-img {
  min-width: 180px; /* Set a fixed width to control item size */
  margin-right: 16px; /* Add space between items */
}




* {
  box-sizing: border-box;
  &:focus {
    outline: none;
  }
}

html,
