.container {
  background-color: #f3fcfd;
  width: 98vw;
  height: 98vh; 
  margin: 20px auto;
  position: relative;
  overflow: hidden;
  border-radius: 19px;
  -webkit-box-shadow: 1px 2px 29px 2px rgba(220, 244, 250, 1);
  -moz-box-shadow: 1px 2px 29px 2px rgba(220, 244, 250, 1);
  box-shadow: 1px 2px 29px 2px rgba(220, 244, 250, 1);
}

.bubble {
  position: absolute;
  border-radius: 50%;
  opacity: 0.7;
  animation: float 10s infinite;
}

.pink-bubble {
  background-color: pink;
  width: 150px;
  height: 150px;
  top: 5%;
  left: 20%;
  animation-delay: 0s;
}

.blue-bubble {
  background-color: lightblue;
  width: 120px;
  height: 120px;
  top: 30%;
  left: 84%;
  animation-delay: 2s;
}

.small-p-bubble {
  background-color: pink;
  width: 60px;
  height: 60px;
  top: 50%;
  left: -3%;
  animation-delay: 4s;
}

.small-b-bubble {
  background-color: lightblue;
  width: 50px;
  height: 50px;
  top: 90%;
  left: 90%;
  animation-delay: 6s;
}

@keyframes float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-30px);
  }
  100% {
    transform: translateY(0);
  }
}

.app-ui {
  width: var(--app-ui-width);
  height: var(--app-ui-height);
  margin: var(--app-ui-margin);
  background-color: var(--app-ui-bg-color);
  border-radius: var(--app-ui-border-radius);
  box-shadow: var(--app-ui-box-shadow);
  position: relative; /* or absolute if needed */
}

.left-ui {
  overflow: hidden;
}

.right-ui {
  width: var(--right-ui-width);
  height: var(--right-ui-height);
  position: absolute;
  left: var(--right-ui-left);
  top: var(--right-ui-top);
  background-color: var(--right-ui-bg-color);
  border-radius: var(--right-ui-border-radius);
